import Image from 'next/image';
import { Typography, Layout, Card, Button, Space, Row, Col, Divider, Affix, TypographyProps } from 'antd';
import { LinkedinOutlined, GithubOutlined, InstagramOutlined, FacebookOutlined, YoutubeOutlined } from '@ant-design/icons';
import Link from 'next/link';
import { useEffect, useRef } from 'react';

const { Title, Text, Paragraph } = Typography;

export default function Home() {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    const width = window.innerWidth;
    const height = window.innerHeight;
    canvas.width = width;
    canvas.height = height;

    // Gradient animation logic
    const points = [];
    for (let i = 0; i < 6; i++) {
      points.push({
        x: Math.random() * width,
        y: Math.random() * height,
        vx: Math.random() * 2 - 1,
        vy: Math.random() * 2 - 1,
        radius: Math.random() * 20 + 5,
        color: `hsl(${Math.random() * 360}, 100%, 50%)`,
      });
    }

    const animate = () => {
      ctx.fillStyle = 'rgba(242, 239, 232, 0.1)';
      ctx.fillRect(0, 0, width, height);

      points.forEach((point, i) => {
        ctx.beginPath();
        ctx.arc(point.x, point.y, point.radius, 0, Math.PI * 2);
        ctx.fillStyle = point.color;
        ctx.fill();

        point.x += point.vx;
        point.y += point.vy;

        if (point.x > width || point.x < 0) point.vx *= -1;
        if (point.y > height || point.y < 0) point.vy *= -1;
      });

      requestAnimationFrame(animate);
    };

    animate();

    return () => {
      // Cleanup
      ctx.clearRect(0, 0, width, height);
    };
  }, []);

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <canvas ref={canvasRef} style={{ position: 'fixed', top: 0, left: 0, zIndex: -1 }} />
      
      <Layout.Content style={{ padding: '2rem', maxWidth: '1200px', margin: '0 auto' }}>
        <Space direction="vertical" size="large" style={{ width: '100%' }}>
          <Title level={1} style={{ color: '#40E0D0', fontSize: '3rem' }}>Welcome to Yabloko Labs!</Title>
          <Paragraph style={{ color: '#fff', fontSize: '1.2rem' }}>
            We're glad you're here. Explore our projects and innovations!
          </Paragraph>

          <Card>
            <Row gutter={[16, 16]}>
              <Col span={24}>
                <Text strong>Our first flagship venture:</Text>
                <Text style={{ color: '#FFD700' }}>Map2Map™</Text>
              </Col>
              <Col span={12}>
                <Image
                  src="/assets/images/logo_map2map.png"
                  alt="Map2Map Logo"
                  width={200}
                  height={100}
                />
              </Col>
              <Col span={12}>
                <Button type="link" href="https://map2map.com" target="_blank">
                  Visit our brand website
                </Button>
              </Col>
            </Row>
          </Card>

          <Card>
            <Row gutter={[16, 16]}>
              <Col span={24}>
                <Text strong>Our newest venture:</Text>
                <Text style={{ color: '#FFD700' }}>AIponATime™</Text>
              </Col>
              <Col span={12}>
                <Image
                  src="/assets/images/logo_aiponatime.png"
                  alt="AIponATime Logo"
                  width={200}
                  height={100}
                />
              </Col>
              <Col span={12}>
                <Button type="link" href="https://www.aiponatime.com" target="_blank">
                  Visit our brand website
                </Button>
              </Col>
            </Row>
          </Card>
        </Space>

        <Divider style={{ margin: '2rem 0' }} />

        <Space size="large">
          <a href="https://www.linkedin.com/in/map2map-yabloko-labs-6b9157364/" target="_blank" rel="noopener">
            <LinkedinOutlined style={{ fontSize: '24px', color: '#0077B5' }} />
          </a>
          <a href="https://github.com/map2map" target="_blank" rel="noopener">
            <GithubOutlined style={{ fontSize: '24px', color: '#333' }} />
          </a>
          <a href="https://www.instagram.com/yablokolabs" target="_blank" rel="noopener">
            <InstagramOutlined style={{ fontSize: '24px', color: '#E1306C' }} />
          </a>
          <a href="https://www.facebook.com/profile.php?id=61577028394576" target="_blank" rel="noopener">
            <FacebookOutlined style={{ fontSize: '24px', color: '#1877F2' }} />
          </a>
          <a href="https://www.youtube.com/@map2map" target="_blank" rel="noopener">
            <YoutubeOutlined style={{ fontSize: '24px', color: '#FF0000' }} />
          </a>
        </Space>
      </Layout.Content>

      <Layout.Footer style={{ textAlign: 'center', padding: '2rem', borderTop: '1px solid #f0f0f0' }}>
        <Space direction="vertical" size="small">
          <Text>© 2025 Yabloko Labs Pvt. Ltd. All rights reserved</Text>
          <Space size="small">
            <Text>CIN: U62011KA2025PTC201171</Text>
            <Text>MSME: UDYAM-KR-03-0533847</Text>
            <Text>DPIIT: DIPP209889</Text>
          </Space>
        </Space>
      </Layout.Footer>
    </Layout>
  );
}
